/*CUSTOM GALLERY*/

.lg-toolbar {
	width: 60px; height: 100%;
	right: 0; left: auto; top: 0;
	background: #000;
}
.lg-sub-html {
	width: 320px;
	height: 100%;
	right: 60px; left: auto; top: 0;
	background: #222222;
	padding: 95px 25px 0 !important;
	text-align: left;
	h4, p {
		color: #d6d6d6;
		font: 400 px_to_rem(17px)/#{1.4} 'Arial';
	}
}

.lg-outer {
	.lg-inner {
	    max-width: calc(100% - 380px);
	    width: calc(100% - 380px);
	}
	.lg-thumb-outer {
	    width: calc(100% - 380px);
	    max-width: calc(100% - 380px);
	}

	.lg-thumb-item {
    	border: none;
    	height: auto;
	}
}

.lg-outer .lg-thumb-item img {
	display: block;
}

.lg-actions {
    position: absolute;
    width: 60px;
    right: 0;
    top: 230px;
    z-index: 99999;
    > div {
    	width: 36px;
	    height: 36px;
	    border-radius: 50% !important;
	    overflow: hidden;
	    border: 1px solid #dedede;
	    position: relative!important;
	    right: 0 !important;
	    left: 0 !important;
	    margin: 16px auto !important;
	    &:before, &:after {
		    left: 50%;
		    top: 50%;
		    position: absolute;
		    transform: translate(-50%, -50%);
	    }
    }
    .lg-next {

    }
    .lg-prev {

    }
}

.lg-outer .lg-thumb-outer.lg-grab .lg-thumb-item:not(.active) {
	position: relative;
	&:before {
		content: "";
		width: 100%; height: 100%;
		position: absolute;
		top: 0; left: 0;
		background: rgba(0, 0, 0, 0.6);
		z-index: 1;
	}
}

.lg-sub-html:empty {
	display: none;
}

.lg-sub-html:empty ~ .lg-inner {
	width: calc(100% - 60px) !important;
	max-width: calc(100% - 60px) !important;
}
.lg-sub-html:empty ~ .lg-thumb-outer {
	width: calc(100% - 60px) !important;
	max-width: calc(100% - 60px) !important;
}

.lg-fullscreen-on {
	/*.lg-inner, .lg-thumb-outer {
		width: calc(100% - 60px) !important;
		max-width: calc(100% - 60px) !important;
	}
	.lg-sub-html {
		display: none;
	}*/
	.lg-thumb-outer {
        -webkit-transform: translate3d(0, 100%, 0) !important;
    	transform: translate3d(0, 100%, 0) !important;
	}
	.lg-toggle-thumb {
		display: none !important;
	}
}

/* END CUSTOM GALLERY*/


