	    border-top: 4px solid #eeeeee;
	}
	.box_300 {
		.title_box_category{
			h4{
				span {
					position: relative;
				    background: #fff;
					font: bold px_to_rem(24px)/#{1.4} $fontmain2; padding-right: 15px;
					color: #333; 
				}
			}
			&:before{
				content:"";
				position: absolute;
